import React, { useMemo } from "react";
import ImageGallery from "react-image-gallery";
import { Divider } from "antd";
import styled from "styled-components";
import { graphql, useStaticQuery } from "gatsby";

const Wrapper = styled.div`
  padding-top: 42px;
  width: 70%;
  margin: 0 auto;
`;

const Title = styled.p`
  font-size: 1rem;
  color: var(--title-color);
  font-weight: bold;
  opacity: 0.85;
  margin-bottom: 0;
  text-align: center;
`;

const Gallery = () => {
  // Query optimized images from Gatsby
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          sourceInstanceName: { eq: "assets" }
          extension: { regex: "/(jpg|jpeg|png)/" }
          name: { regex: "/^[0-9]+-[0-9]+$/" }
        }
        sort: { fields: name, order: ASC }
      ) {
        nodes {
          id
          name
          childImageSharp {
            # Thumbnail - small for the bottom slider
            thumbnail: gatsbyImageData(
              width: 100
              height: 75
              quality: 70
              placeholder: BLURRED
              formats: [AUTO, WEBP]
            )
            # Original - optimized for main display
            original: gatsbyImageData(
              width: 1200
              quality: 80
              placeholder: BLURRED
              formats: [AUTO, WEBP]
            )
          }
        }
      }
    }
  `);

  // Convert Gatsby images to react-image-gallery format
  const images = useMemo(() => {
    return data.allFile.nodes.map((node) => {
      const thumbnail = node.childImageSharp.thumbnail.images.fallback.src;
      const original = node.childImageSharp.original.images.fallback.src;
      
      return {
        original: original,
        thumbnail: thumbnail,
        loading: "lazy",
      };
    });
  }, [data]);

  return (
    <Wrapper>
      <Divider style={{ marginTop: 0, marginBottom: 32 }} plain>
        <Title>  </Title>
      </Divider>
      <ImageGallery
        showPlayButton={false}
        showFullscreenButton={false}
        items={images}
        lazyLoad={true}
      />
    </Wrapper>
  );
};

export default Gallery;
